<template>
  <div class="search">
    <div class="search-box-wrapper" >
      <search-box ref="searchBox" @query="onQueryChange"></search-box>
    </div>
    <div class="shortcut-wrapper" v-show="!query" ref="shortcutWrapper">
	  	<scroll class="shortcut" :data="shortcut" ref="shortcut">
	  		<div>
	  			<div class="hot-key">
	  			<h1 class="title">热门搜索</h1>
	  			<ul>
	  				<li @click="addQuery(item.k)" class="item" v-for="item in hotKey">
	  					<span>{{item.k}}</span>
	  				</li>
	  			</ul>
	  			</div>
	        	<div class="search-history" v-show="searchHistory.length">
	          <h1 class="title">
	            <span class="text">搜索历史</span>
	            <span @click="showConfirm" class="clear">
	              <i class="icon-clear"></i>
	            </span>
	          </h1>
	          <search-list @delete="deleteOne" @select="addQuery" :searches="searchHistory"></search-list>
	        	</div>
	        </div>
	  	</scroll>
  	</div>
  	<div class="search-result" v-show="query" ref="searchResult">
  		<suggest :query="query" ref="suggest" @listScroll="blurInput" @select="saveSearch"></suggest>
  	</div>
  	<confirm ref="confirm" @confirm="clearSearchHistory" text="是否清空所有搜索历史" confirmBtnText="清空"></confirm>
  	<router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import SearchBox from 'base/search-box/search-box'
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import {ERR_OK} from 'api/config'
  import {playlistMixin, searchMixin} from 'common/js/mixin'
  import {mapActions, mapGetters} from 'vuex'
  import {getHotKey} from 'api/search'
  import SearchList from 'base/search-list/search-list'
  import Suggest from 'components/suggest/suggest'
  export default {
  	mixins: [playlistMixin],
  	components: {
  		SearchBox,Suggest,SearchList,Confirm,Scroll
  	},
  	created() {
  		this._getHotKey()
  	},
  	data() {
  		return {
  			hotKey: [],
  			query: ''
  		}
  	},
    computed: {
    	shortcut() {
    		return this.hotKey.concat(this.searchHistory)
    	},
      	...mapGetters([
          	'searchHistory',
        ])
    },
  	methods: {
  		handlePlaylist(playlist) {
  			const bottom = playlist.length > 0 ? '60px' : ''
  			this.$refs.shortcutWrapper.style.bottom = bottom
  			this.$refs.shortcut.refresh()
  			this.$refs.searchResult.style.bottom = bottom
  			this.$refs.suggest.refresh()
  		},
      showConfirm() {
      	this.$refs.confirm.show()
      },
      deleteOne(item) {
        this.deleteSearchHistory(item)
      },
      saveSearch() {
        this.saveSearchHistory(this.query)
      },
      blurInput() {
        this.$refs.searchBox.blur()
      },
  	  onQueryChange(query) {
		this.query = query
  	  },
  		addQuery(query) {
  			this.$refs.searchBox.setQuery(query)
  		},
  		_getHotKey() {
  			getHotKey().then((res) => {
  				if(res.code === ERR_OK) {
  					this.hotKey = res.data.hotkey.slice(0,10)
  				}
  			})
  		},
      ...mapActions([
        'saveSearchHistory',
        'deleteSearchHistory',
        'clearSearchHistory'
        ])
  	},
  	watch: {
  		query(newQuery) {
  			if(!newQuery) {
  				setTimeout(() => {
  					this.$refs.shortcut.refresh()
  				})
  			}
  		}
  	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .search
    .search-box-wrapper
      margin: 20px
    .shortcut-wrapper
      position: fixed
      top: 178px
      bottom: 0
      width: 100%
      .shortcut
        height: 100%
        overflow: hidden
        .hot-key
          margin: 0 20px 20px 20px
          .title
            margin-bottom: 20px
            font-size: $font-size-medium
            color: $color-text-l
          .item
            display: inline-block
            padding: 5px 10px
            margin: 0 20px 10px 0
            border-radius: 6px
            background: $color-highlight-background
            font-size: $font-size-medium
            color: $color-text-d
        .search-history
          position: relative
          margin: 0 20px
          .title
            display: flex
            align-items: center
            height: 40px
            font-size: $font-size-medium
            color: $color-text-l
            .text
              flex: 1
            .clear
              extend-click()
              .icon-clear
                font-size: $font-size-medium
                color: $color-text-d
    .search-result
      position: fixed
      width: 100%
      top: 178px
      bottom: 0
</style>


